<template>
  <div class="nav">
    <div class="container">
      <el-row>
        <el-col :span="12">
          <router-link class="anchor" to="/">首页</router-link>
          <a class="anchor" href="#">item</a>
        </el-col>
        <el-col class="text-right" :span="12">
          <span @click="ui.showCart = !ui.showCart" class="anchor">购物车 <span>({{cartCount}})</span></span>
          <!--<a class="anchor" href="#">item</a>-->
          <span v-if="session.loggedIn()">
            <router-link class="anchor"
                         to="/my/setting">{{session.user('nickname') || session.user('mail') || session.user('phone') | cut(6) }}</router-link>
            <span @click="session.logout()" class="anchor">登出</span>
          </span>
          <span v-else>
            <router-link class="anchor" to="/login">登录</router-link>
            <router-link class="anchor" to="/signup">注册</router-link>
          </span>
        </el-col>
      </el-row>
    </div>
    <Cart @close="() => {ui.showCart = false}" v-show="ui.showCart"/>
  </div>
</template>

<script>
  import session     from '../lib/session';
  import Cart        from './Cart';
  import cartService from '../service/cart';

  export default {
    components : { Cart },
    mounted () {
      // 当购物车内发生改变，显示多少种货
      cartService.onChange(_ => {
        this.cartCount = cartService.productCount();
      });
    },
    data () {
      return {
        cartCount : 0,
        ui        : {
          showCart : false,
        },
        session,
      };
    },
  };
</script>

<style scoped>
  .nav {
    background: rgba(0, 0, 0, .1);
  }

  .nav .anchor {
    padding: .6em;
  }
</style>