// 购物车数据，需要考虑 登陆 和 没登录 两种情况 数据需要同步 服务端和本地数据需要一个同步的过程
// 让数据单项流动 在本地添加后 sync 一下 同步到服务器端
// change 添加 product_id 某一种商品, count 某一种商品数量,  product_snapshot 某一种商品数据 如果购物车没有这种商品就直接初始化一个 有的话直接加减数量

import session     from '../lib/session';
import store       from '../lib/store';
import { obj2Arr } from '../lib/helper';
import api         from '../lib/api';

// localCart 购物车所有的数据 最鲜活的最新的数据 在内存中 它一旦改变才会引起 local 和 服务器端数据的改变
let localCart      = {
  // 1: {              数据结构 
  //   product: {
  //      ...
  //   }
  //   count: 2
  // },
  // 2: {
  //   count :2
  // }
};
// 回调池 当 localCart 发生改变 我需要执行的回调函数
const callbackPool = [];

const output = {
  // 当 localCart 发生改变时，传入一个函数，如果函数已有就返回，否则就添加到 callbackPool 数组里
  onChange (fn) {
    if (callbackPool.find(it => it === fn))
      return;

    callbackPool.push(fn);

    // 保证可以收到初始数据
    this.callPool();
  },
  /**
   * 更改购物车中的某个产品的记录
   * @param {number} product_id 产品id
   * @param {number} count 数量
   * @param {Object} product_snapshot 对应产品数据
   */
  change (product_id, count, product_snapshot, prop, user_id) {
    if (!user_id)
      user_id = session.user('id');

    //   往购物车里添加一个商品 如果没有 就初始化一个  product_snapshot 就是为了初始化商品数据
    if (!localCart[ product_id ])
      localCart[ product_id ] = { product_id, count : 0, product_snapshot, prop, user_id };

    // 否则就让那件商品增加数量一个
    let finalCount = localCart[ product_id ].count += count;

    // 购物车中某一项的累计数量结果 等于0就直接删掉那一项
    if (finalCount <= 0)
      this.remove(product_id);

    // callPool 更新本地数据；    sync 上传到服务端
    this.callPool();
    this.sync();
  },

  // 循环 callbackPool 里所有的函数 然后把当前最新的 localCart 数据回传回去 返回的值必须是重新拷贝一份的值，否则系统会认定是同一个值导致购物车页面不实时渲染 （排错很久）
  callPool () {
    callbackPool.forEach(fn => fn({ ...localCart }));
  },

  // 拿取本地最新数据 返回的值必须是重新拷贝一份的值，否则系统会认定是同一个值导致购物车页面不实时渲染 （排错很久）
  get () {
    return { ...localCart };
  },

  // 删除购物车某一种 并执行 callPool 函数 把当前最新的 localCart 数据回传回去
  remove (product_id) {
    delete localCart[ product_id ];
    this.callPool();
  },

  // 清空购物车
  clear () {
    localCart = {};
    this.callPool();
    this.sync();
  },

  // 和 sync 同步正好相反 拿取本地local的数据渲染在购物车上
  restore () {
    this.restoreLocal();
    this.callPool();
  },

  // 如果是一个新用户，就该是一个空值 {}  内存中 localCart 等于 本地local的数据
  restoreLocal () {
    localCart = store.get('cart') || {};
  },

  // 将内存数据同步到 本地local 和 服务器端
  sync () {
    this.syncCloud();
    this.syncLocal();
  },

  // 如果登陆了，将内存中数据更新存储到服务器端
  syncCloud () {
    if (!session.loggedIn())
      return;

    let idList = [];
    this
      //  当前这个用户在云端一共有多少条记录
      .getCloud()
      .then(r => {
        let data = r.data || [];

        // 收集所有记录的id: [1, 2, 3]
        data.forEach(row => {
          idList.push(row.id);
        });

        // 清空当前用户的云端购物车数据
        this.clearCloud(idList);
        // 清掉之后将最新的数据上传到云端
        this.overwriteCloud();
      });
  },

  // 本地没有数据时，登陆将数据下载到本地
  restoreCloud () {
    this.getCloud({ key_by : 'product_id' })
        .then(r => {
          localCart = r.data;
          this.syncLocal();
          this.callPool();
        });
  },

  // 内存中最新的数据上传到云端
  overwriteCloud () {
    api('cart/create_many', {
      data : obj2Arr(localCart),
    });
  },

  // 获取云端数据 发请求
  getCloud (param = {}) {
    return api('cart/read', { ...param, where : { and : { user_id : session.user('id') } } });
  },

  // 清空云端数据
  clearCloud (idList) {
    if (!idList.length)
      return;

    return api('cart/delete_many', { in : idList });
  },

  // 将内存中数据更新存储到本地 localStorage  初始化为一个空对象
  syncLocal () {
    store.set('cart', localCart || {});
  },

  //  这个函数是显示导航栏购物车有多少种货
  productCount () {
    localCart = localCart || {}
    return Object.keys(localCart).length;
  },

  // 转成布尔值
  isEmpty () {
    return !this.productCount();
  },
};

//   一开始，先从本地local拿一次数据渲染到购物车界面上
init();

function init () {
  if(session.isAdmin())
    return;

  output.restore();
  if (session.loggedIn())
    output.restoreCloud();
}

export default output;