<template>
  <!-- 此组件只负责拿取到搜索的关键字 并跳转到搜索页 同时携带这关键字传参 -->
  <div class="nav">
    <div class="container">
      <el-row :gutter="10">
        <el-col :span="6">
          <router-link to="/">
            <div class="logo">
              <img src="../img/logo@2x.png" alt="Logo">
            </div>
          </router-link>
        </el-col>
        <el-col class="text-right" :span="12">
          <form @submit.prevent="toSearch" class="search">
            <el-input v-model="formSearch.keyword" suffix-icon="el-icon-search"></el-input>
          </form>
        </el-col>
        <el-col :span="6">
          <div class="logo">
          </div>
        </el-col>
      </el-row>
    </div>
  </div>

</template>

<script>
  import logo from '../img/logo.png';

  export default {
    data () {
      return {
        formSearch : {},
        logo,
      };
    },
    methods : {
      // 搜索之后 跳转至搜索页面
      toSearch () {
        this.$router.push({
          path   : '/search',
          // query 是 vue 提供的一种传参方式 ？ 后面用 & 隔开
          query : this.formSearch,
        });
      },
    },
  };
</script>

<style scoped>
  .logo {
    max-width: 10em;
    padding-top: 1em;
    padding-bottom: 1em;
  }

  .search {
    position: relative;
    top: 15px;
  }
</style>