<script>
  import RegularNav      from '../components/RegularNav';
  import { fileUrl }     from '../lib/helper';
  import { createOrder } from '../lib/order';
  import session         from '../lib/session';
  import cartService     from '../service/cart';
  import holder          from '../config/holder';

  export default {
    components : { RegularNav },
    name: 'Product',
    data () {
      return {
        holder,
        cartService,

        form : {
          count : 1,
          prop  : {},
        },
        row  : {
          id : null,
        },
      };
    },
    mounted () {
      this.row.id = this.$route.params.id;
      this.find();
    },
    methods    : {
      fileUrl,

      // 当点击个性属性执行的函数， 比如说  辣度：特辣 
      setProp (key, value) {
        this.$set(this.form.prop, key, value);
      },

      // 添加到购物车
      addToCart () {
        let row  = this.row;
        let form = this.form;

        if (!this.validateForm())
          return;

        cartService.change(row.id, form.count, row, form.prop);
      },

      // 点击购买该执行的函数， product_snapshot 当前要购买商品信息的快照
      createOrder () {
        let p = this.row;
        let f = this.form;

        f.product_id       = p.id;
        f.product_snapshot = p;

        // order 是一条订单数据，而 this.form 仅仅是一个商品 给 order 自定义添加的 detail 属性  再添加 sum 属性，  detail: [this.form] 允许添加多条也就是可以购买多个 购物车的形式
        // let order = { detail: [this.form] }
        // order.sum = orderSum(order.detail);
        // order.user_id = session.user('id');
        // order.status = 'created';

        if (!this.validateForm())
          return;

        // 点击购买之后，直接跳转至订单详情页
        // api('order/create', order)
        //   .then( (r) => {
        //     this.$router.push(`/my/order/${r.data.id}`)
        //   });

        // 重构直接购买功能  this.form 提交的那个表单  session.user('id') 就是谁的订单  （重构之后，订单全部由 order.js 生成的）
        createOrder([ f ], session.user('id'))
          .then(r => {
            this.$router.push(`/my/order/${r.data.id}`);
          });
      },

      // 添加前先验证
      validateForm () {
        if (session.isAdmin()) {
          alert('管理员暂不可购买商品');
          return;
        }

        if (!this.allPropsChecked()) {
          alert('请选择所有属性选项');
          return false;
        }

        if (!this.form.count) {
          alert('请选择数量');
          return false;
        }

        return true;
      },

      // 在创建一条订单之前，首先要验证 每个个性属性都必须有选中的选项  循环prop里面所有的个性属性，一旦有没选中的就返回false
      allPropsChecked () {
        let p = this.row.prop;
        for (let key in p) {
          if (!this.form.prop[ key ])
            return false;
        }

        return true;
      },

      find () {
        api('product/find', this.row)
          .then(r => {
            this.row = r.data;
            this.normalize();
          });
      },

      // 拿到数据后分割好渲染 用 propList 替换掉 row 里面的 prop
      normalize () {
        let p          = this.row.prop;
        let betterProp = {};

        for (let key in p) {
          let options       = p[ key ];
          betterProp[ key ] = options.split('|');
        }

        this.$set(this.row, 'prop', betterProp);
      },
    },
  };
</script>

