<template>
  <el-row>
    <el-col :span="3" class="type">{{propName}}</el-col>
    <el-col :span="21">
      <div 
        @click="select(it)" 
        :key="it" 
        v-for="it in data" 
        :class="'option ' + ((selected && (selected.id == it.id)) ? 'active' : '')"
      >
        {{it.name}}
      </div>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    props   : [ 'data', 'propName', 'initial' ],
    data () {
      return {
        selected : null,      // 对象结构
      };
    },
    mounted() {
      // 如果父元素传了 initial 某一种属性分类下的某一项，则一开始就选中那一项并筛选结果
      if(this.initial)
        this.selected = this.initial;
    },
    methods : {
      // it 一个种类项， 如果某一个种类当前被选中 且 再次被选中 就取消被选中且清空当前符合选中条件的商品
      select (it) {
        if (this.selected && (this.selected.id == it.id)) {
          this.selected = null;
          this.$emit('clear', it);
          return;
        }
        // 否则就换 子父通信 
        this.selected = it;
        this.$emit('change', it);
      },
    },
    // 监听 一旦 initial 有变化执行 select 传入最新的值
    watch: {
      initial(n) {
        this.select(n);
      }
    }
  };
</script>

<style scoped>
  .filter .type,
  .filter .option {
    display: inline-block;
    cursor: pointer;
    padding: 1em;
  }

  .filter .option.active {
    background: #409EFF;
    color: #fff;
  }
</style>