<template>
  <div>
    <div class="container">
      <el-row :gutter="10">
        <el-col :span="6">
          <router-link to="/">
            <div class="logo">
              <img src="../img/logo@2x.png" alt="Logo">
            </div>
          </router-link>
        </el-col>
        <el-col class="text-right" :span="12">
          <form @submit.prevent="reload" class="search">
            <el-input v-model="q.keyword" suffix-icon="el-icon-search"></el-input>
          </form>
        </el-col>
        <el-col :span="6">
          <div class="logo">

          </div>
        </el-col>
      </el-row>
    </div>
    <div class="container">
      <div class="filter">
        <div class="table">
          <FilterProp @change="setPropFilter('cat_id', $event)"
                      @clear="removePropFilter('cat_id')"
                      propName="分类"
                      :initial="filterRestore.cat_id"
                      :data="listCat"/>
          <FilterProp @change="setPropFilter('brand_id', $event)"
                      @clear="removePropFilter('brand_id')"
                      propName="品牌"
                      :initial="filterRestore.brand_id"
                      :data="listBrand"/>
        </div>
        <div class="bar">
          <div class="group">
            <el-button-group>
              <el-button :type="q.sortBy == 'id' ? 'primary' : ''" @click="setSortBy('id')" size="mini">新品
                <span v-if="q.sortBy == 'id'">
                  <i v-if="q.sortUp == 1" class="el-icon-caret-top"></i><i v-else class="el-icon-caret-bottom"></i>
                </span>
              </el-button>
              <el-button :type="q.sortBy == 'price' ? 'primary' : ''" @click="setSortBy('price')" size="mini">价格
                <span v-if="q.sortBy == 'price'">
                  <i v-if="q.sortUp == 1" class="el-icon-caret-top"></i><i v-else class="el-icon-caret-bottom"></i>
                </span>
              </el-button>
              <el-button :type="q.sortBy == 'sales' ? 'primary' : ''" @click="setSortBy('sales')" size="mini">销量
                <span v-if="q.sortBy == 'sales'">
                  <i v-if="q.sortUp == 1" class="el-icon-caret-top"></i><i v-else class="el-icon-caret-bottom"></i>
                </span>
              </el-button>
            </el-button-group>
          </div>
          <div id="price-filter" class="group">
            <el-col :span="12">
              <input @keyup="reload"
                     v-model="q.minPrice"
                     size="mini"
                     placeholder="最底价格"
              >
              </input>
            </el-col>
            <el-col :span="12">
              <input @keyup="reload"
                     v-model="q.maxPrice"
                     size="mini"
                     placeholder="最高价格"
              >
              </input>
            </el-col>
          </div>
          <div class="group">
            <label>
              <input @click="toggleBool('freeShipping')"
                     :checked="q.freeShipping"
                     type="checkbox">
              包邮
            </label>
            <label>
              <input @click="toggleBool('hasDiscount')"
                     :checked="q.hasDiscount"
                     type="checkbox">
              折扣
            </label>
            <label>
              <input @click="toggleBool('cod')"
                     :checked="q.cod"
                     type="checkbox">
              货到付款
            </label>
          </div>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="result">
        <el-row :gutter="5" class="card-list vertical-gutter">
          <el-col :key="it" v-for="it in list" :span="6">
            <ProductCard :data="it"/>
          </el-col>
        </el-row>
        <el-pagination v-if="total != 0"
                       :total="total"
                       class="text-center"
                       layout="prev, pager, next"
                       :page-count="q.limit"
                       :current-page="q.page"  
                       @current-change="flip"
        >              <!-- current-page 一开始等于空 当变化了却感应不到变化  所以用 v-if="total != 0 -->
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import RegularNav     from '../components/RegularNav';
  import ProductCard    from '../components/ProductCard';
  import { capitalize } from '../lib/helper';
  import FilterProp     from '../components/search/FilterProp';

  export default {
    components : { FilterProp, ProductCard, RegularNav },
    data () {
      return {
        list          : [],    // 用来存储 搜索查询返回来的数据
        total         : 0,     // 用来存储 搜索查询返回来的总数
        listCat       : [],    // 用来存储 分类返回来的数据
        listBrand     : [],    // 用来存储 品牌返回来的数据
        filterRestore : {
          // brand_id: {           数据结构就是这样
          //   id: 1,
          //   name: 'lcd'
          // }
        },
        q             : { // 地址传参的暂存区（this.$route.query的拷贝） 一开是在 mounted 里拷贝 后面变化在 $route 里拷贝
          page  : 1,
          limit : 16,
          sortBy: 'id'
        },
      };
    },

    mounted () {
      this.q = { ...this.q, ...this.$route.query, page : parseInt(this.$route.query.page) };  // 直接先把需要的数据拷贝到 q 里面
      this.read('cat');                                                                       // 查询一次 cat 分类
      this.read('brand');                                                                     // 查询一次 brand 分类
      this.search();                                                                          
      this.restoreSelectedPropFilter('brand', this.q[ '@brand_id' ], 'brand_id');             // 通过属性筛选其中某一项
      this.restoreSelectedPropFilter('cat', this.q[ '@cat_id' ], 'cat_id');                   // 通过属性筛选其中某一项
    }, 
    methods    : {
      /** 
       * 通过属性过滤器的已有传参找到对应的数据 如：'http://...?...&@cat_id=1' 就应该寻找id = 1的那条cat数据，并将其
       * 存入this.filterRestore内部，然后将其 传入<FilterProp/>组件中的initial属性中：<FilterProp :initial="filterRestore.cat_id"/>
       *
       * @param model        比如 cat 或 brand
       * @param id           比如  this.q[ '@brand_id' ] 或 this.q[ '@cat_id' ]
       * @param filterName   比如 brand_id 或 cat_id
       */
      restoreSelectedPropFilter (model, id, filterName) {
        // 没有选择  cat 或 brand 中的任何选项 就不用筛选全部显示
        if (!id)
          return;
        // 找到 cat 或 brand 下的某一项
        api(`${model}/find`, { id })
          .then(r => {
            this.$set(this.filterRestore, filterName, r.data);
          });
      },

      // 通过子父通信的自定义事件函数 当某一个种类发生改变，筛选的商品也要跟着变  prop : cat_id 或 brand_id，  it 就是分类里面选中的那一项
      setPropFilter (prop, it) {
        this.q[ '@' + prop ] = it.id;
        this.reload();
      },

      //  通过子父通信的自定义事件函数  清空符合的筛选项  prop : cat_id 或 brand_id
      removePropFilter (prop) {
        delete this.q[ '@' + prop ];
        this.reload();
      },

      //  用来查询 拿到整个分类 或 品牌分类下的商品数据
      read (type) {
        api(`${type}/read`)
          .then(r => this[ 'list' + capitalize(type) ] = r.data);
      },

      // 筛选函数 通过包邮折扣还是货到付款 来对商品分类  如果筛选已经存在说明已有键名 type 就删掉它，否则就显示 筛选键名等于1
      toggleBool (type) {
        if (this.q[ type ])
          delete this.q[ type ];
        else
          this.q[ type ] = '1';

        this.reload();
      },

      // 筛选函数 通过新品价格还是销量 来对商品排序
      setSortBy (type) {
        // 首先判断是 新品价格还是销量
        if (this.q.sortBy == type) {
          // 再次点击 然后再判断 如果是升序再点一下就 else 否则就等于 1 
          if (!this.q.sortUp)
            this.q.sortUp = '1';
          else
            delete this.q.sortUp;
        }

        this.q.sortBy = type;

        this.reload();
      },

      // 提交表单之后 路由 $router 发生变化 watch 肯定会执行  （也就是说提交搜索之后，先触发 reload ()， 改变路由。再在 watch 中监控改变搜索页面 ）
      reload () {
        this.$router.push({
          path  : '/search',
          query : { ...this.q },
        });
      },
      forEachPropFilter (fn) {
        let q = this.q;
        for (let key in q) {
          if (!key.startsWith('@'))
            continue;

          fn(q[ key ], key);
        }
      },

      // 翻页
      flip (page) {
        this.q.page = page;
        this.reload();
      },

      // 真正发请求，开始搜索 
      search () {
        let q = this.q;

        let keywordQuery      = `"title" contains "${q.keyword}"`;
        let minPriceQuery     = q.minPrice ? `and "price" >= ${q.minPrice}` : '';
        let maxPriceQuery     = q.maxPrice ? `and "price" <= ${q.maxPrice}` : '';
        let freeShippingQuery = q.freeShipping ? `and "shipping_fee" = 0` : '';
        let hasDiscountQuery  = q.hasDiscount ? `and "discount" > 0` : '';
        let codQuery          = q.cod ? `and "cod" = 1` : '';
        let propFilterQuery   = '';     

        /**
         * 本质就是将上面的这种类型 转换为 下面的查询语句类型   （分类 品牌 多个大分类下的筛选）
         * {
         *   '@cat_id': 1
         *   '@brand_id': 2
         *   'sortBy': ['a']
         * }
         * 转换后
         * 'and "cat_id" = 1 and "brand_id" = 2'
         */
        this.forEachPropFilter((value, key) => {
          let prop = key.slice(1);
          propFilterQuery += ` and "${prop}" = "${value}" `;
        });

        // 所有的 子查询条件 比如有没有 关键字 最低价格 最高价格 包邮 折扣 货到付款等，  propFilterQuery 通过某一种分类或品牌分
        let query = `where(
          ${keywordQuery}
          ${minPriceQuery}
          ${maxPriceQuery}
          ${freeShippingQuery}
          ${hasDiscountQuery}
          ${codQuery}
          ${propFilterQuery}
        )`;

        let param = {
          query,
          sort_by : [ (q.sortBy || 'id'), q.sortUp ? 'up' : 'down' ],     // 是生序排列还是降序
          limit   : this.limit,
          page    : q.page,
          limit   : q.limit,
        };

        api('product/read', param)
          .then(r => {
            this.list  = r.data;
            this.total = r.total;
          });
      },
    },

    // RegularNav 组件中有了关键字的变化 所以才监控 $route 的变化 一切都是通过 $route 触发的 ， 一旦 $route 发生变化 this.q 就拷贝一份
    watch      : {
      $route : {
        deep : true,
        // 一旦路由发生变化， handler 将会执行，再去搜索
        handler (news, old) {
          this.q = { ...this.q, ...news.query, page : parseInt(news.query.page) };
          this.search();
        },
      },
    },
  };
</script>

<style scoped>
  .search {
    margin-top: 1em;
    margin-bottom: 1em;
  }

  .filter {
    color: #666;
  }

  .filter > * {
    margin-bottom: .5em;
  }

  .filter .table > * {
    border: 1px solid rgba(0, 0, 0, .1);
    border-bottom-width: 0;
  }

  .filter .table > *:last-child {
    border-bottom-width: 1px;
  }

  .filter .type {
    font-size: 90%;
    opacity: .8;
  }


  .bar > * {
    display: inline-block;
    vertical-align: middle;
  }

  .bar .group {
    margin-right: .5em;
  }

  #price-filter {
    max-width: 14em;
  }

  #price-filter input {
    width: 100%;
  }


  .el-checkbox {
    margin-right: .5em;
  }
</style>