<template>
  <div class="cart">
    <el-card>
      <div slot="header" class="clearfix">
        <span>我的购物车</span>
        <span class="anchor" @click="$emit('close')" style="float: right;">关闭</span>
      </div>
      <div v-if="Object.keys(listCart).length < 1">暂无内容</div>
      <el-row :key="k" v-for="(v, k) in listCart">
        <el-col :span="4">
          <ProductThumb style="padding: .2em;" :data="v"/>
        </el-col>
        <el-col :span="10">{{v.product_snapshot.title}}</el-col>
        <el-col class="cny hot text-center" :span="4">{{v.product_snapshot.price}}</el-col>
        <el-col :span="6">
          <div class="el-input-number el-input-number--mini">
            <span 
              @click="cartService.change(v.product_snapshot.id, -1)" 
              role="button"
              class="el-input-number__decrease"
            >
              <i class="el-icon-minus"></i>
            </span>
            <span 
              @click="cartService.change(v.product_snapshot.id, +1)" 
              role="button"
              class="el-input-number__increase"
            >
              <i class="el-icon-plus"></i>
            </span>
            <div class="el-input el-input--mini">
              <input type="text"
                     v-model="v.count"
                     autocomplete="off"
                     aria-label="描述文字"
                     max="Infinity"
                     min="1"
                     class="el-input__inner"
                     role="spinbutton"
                     aria-valuemax="10"
                     aria-valuemin="1"
                     aria-valuenow="4"
                     aria-disabled="undefined">
            </div>
          </div>
        </el-col>
      </el-row>
      <p>
        总价：<span class="hot cny">{{orderSum}}</span>
      </p>
      <p>
        <el-button @click="createOrder" type="primary">付款</el-button>
      </p>
    </el-card>
  </div>
</template>

<script>
  import cartService     from '../service/cart';
  import ProductThumb    from '../components/ProductThumb';
  import session         from '../lib/session';
  import { createOrder, orderSum } from '../lib/order';
  import { obj2Arr }     from '../lib/helper';

  export default {
    components : { ProductThumb },
    data () {
      return {
        listCart : {},
        cartService,
      };
    },
    computed: {
      listCartArr() {
        return obj2Arr(this.listCart);
      },
      orderSum() {
        return orderSum(this.listCartArr);
      }
    },
    mounted () {
      console.log(cartService.get());
      // 一开始 手动调一次数据
      // this.cart_list = cartService.get();

      // 购物车数据发生改变 ，让回调传回来的新数据重新覆盖 cart_list 里的数据
      cartService.onChange(localCart => this.listCart = localCart);
    },
    methods    : {
      createOrder () {
        createOrder(this.listCartArr, session.user('id'))   //  session.user('id') 谁创建的订单
          .then(r => {
            this.$router.push(`/my/order/${r.data.id}`);
            cartService.clear();
            this.$emit('close')
          });
      },
    },
  };
</script>

<style scoped>
  h2 {
    text-align: center;
  }

  .cart {
    position: fixed;
    max-width: 400px;
    min-width: 200px;
    z-index: 10;
    right: 15px;
  }

  .el-input-number--mini {
    max-width: 100px;
  }
</style>

