<template>
  <div>
    <h1>设置</h1>
    <div class="toolbar">
      <button @click="editMode = !editMode">
        <span v-if="editMode">取消</span>
        <span v-else>编辑</span>
      </button>
    </div>
    <form @submit.prevent="update">
      <fieldset :disabled="updatePending">
        <dl class="pair">
          <dt>昵称</dt>
          <dd>
            <span v-if="editMode">
              <input class="field" type="text" v-model="me.nickname" :readonly="!editMode">
            </span>
            <span v-else>
              <span>{{me.nickname || '-'}}</span>
            </span>
          </dd>
        </dl>
        <dl class="pair">
          <dt>手机号</dt>
          <dd>
            <span v-if="editMode">
              <input class="field" type="text" v-model="me.phone" :readonly="!editMode">
              <span v-if="me.phone != meCopy.phone && validMe">
                <input type="text" v-model="tmp.captchaInput">
                <Captcha @send="storeCaptcha" send-by="phone" :receiver="me.phone"/>
              </span>
            </span>
            <span v-else>
              <span>{{me.phone || '-'}}</span>
            </span>
          </dd>
        </dl>
        <dl class="pair">
          <dt>邮箱</dt>
          <dd>
            <span v-if="editMode">
              <input class="field" type="text" v-model="me.mail" :readonly="!editMode">
              <span v-if="me.mail != meCopy.mail && validMe">
                <input type="text" v-model="tmp.captchaInput">
                <Captcha 
                  @send="storeCaptcha" 
                  send-by="mail" 
                  :receiver="me.mail"
                />
              </span>
            </span>
            <span v-else>
              <span>{{me.mail || '-'}}</span>
            </span>
          </dd>
        </dl>
        <div class="error-list">
          <div :key="e" v-for="e in errorMsg" class="error">{{e}}</div>
          <div v-if="invalidCaptcha" class="error">验证码有误</div>
        </div>
        <div v-if="editMode">
          <button class="el-button--primary" type="submit">完成</button>
        </div>
      </fieldset>
    </form>
  </div>
</template>

<script>
  import session from '../lib/session';
  import Captcha from '../components/Captcha';
  import { is }  from '../lib/valee';

  export default {
    components : { Captcha },
    data () {
      return {
        invalidCaptcha : false,     // 保存验证码正确或有误的状态，默认没有错 true表示有误
        tmp            : {},        // 用来临时放数据 与原始数据对比
        me             : {},        // 当前用户的数据
        meCopy         : {},
        editMode       : false,     // 是否为编辑模式
        updatePending  : false,     // 默认没有更新
        errorMsg       : [],
      };
    },
    computed   : {
      validMe () {
        return !this.errorMsg.length;
      },
    },
    mounted () {
      this.findMe();
    },
    methods    : {

      // 验证
      validate () {
        this.errorMsg = [];

        let me = this.me;
        if (!is.phone(me.phone))
          this.errorMsg.push('手机号不合法');

        if (!is.mail(me.mail))
          this.errorMsg.push('邮箱不合法');

        // 如果phone更改了，就验证 verifyCaptcha()
        if (this.changed('phone'))
          if (!this.verifyCaptcha())
            this.invalidCaptcha = true;

        // 如果mail更改了，就验证 verifyCaptcha()
        if (this.changed('mail'))
          if (!this.verifyCaptcha())
            this.invalidCaptcha = true;

        return !this.errorMsg.length && !this.invalidCaptcha;
      },

      // 拿到当前用户数据 并拷一份给 meCopy 用开分辨电话是否有修改
      findMe () {
        api('user/find', { id : session.user('id') })
          .then(r => {
            this.me = r.data;
            this.copyMe();
          });
      },

      // 拷贝一份当前用户的数据
      copyMe () {
        this.meCopy = { ...this.me };
      },

      // 自定义函数 用来接收子组件传来的验证码 存放在 tmp.captchaAnswer
      storeCaptcha (code) {
        this.tmp.captchaAnswer = code;
      },

      // 验证码比对  手机或邮箱传过来的验证码 ： 输入的验证码
      verifyCaptcha () {
        return this.tmp.captchaAnswer == this.tmp.captchaInput;
      },

      // prop 用户的某个属性 比如用户名，  如果那个值不等于拷贝一份的值 就说明更改了
      changed (prop) {
        return this.me[ prop ] !== this.meCopy[ prop ];
      },

      // 更新用户数据 并拷一份给 meCopy 用开分辨电话是否有修改
      update () {
        if (!this.validate())
          return;

        this.updatePending = true;

        api('user/update', this.me)
          .then(r => {
            let me             = this.me = r.data;
            this.editMode      = false;
            this.updatePending = false;
            this.copyMe();
            session.login(me.id, me, _ => {location.reload();});
          });
      },
    },
    watch      : {
      me : {
        deep : true,
        handler () {
          if (this.editMode)
            this.validate();
        },
      },
    },
  };
</script>